import React, { Component } from "react";

import Countdown from "react-countdown-now";

import "./info-box.css";
import Background from "../assets/pan-grad.png";

const Completionist = () => <span>Hack-A-Ton</span>;

const render = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    return <Completionist />;
  } else {
    return(
    <span>
      {days}<span className="tiempo" >D</span>{' '}
      {hours.toString().padStart(2, '0')}<span className="tiempo">h </span>
      {minutes.toString().padStart(2, '0')}<span className="tiempo">m </span>
      {seconds.toString().padStart(2, '0')}<span className="tiempo">s </span>
    </span>
    )
  }
  
}

export class InfoBox extends Component {
  render() {
    return (
      <div className="backgroundHeader" style={{
        backgroundImage: `url(${Background})`,
        backgroundSize: "cover",
        backgroundAttachment: "fixed"
      }}>
      <div className="upperImageContainer">
        <div className="BigText">
          <Countdown date={new Date('March 28, 2019 11:00:00')} renderer={render}/>
        </div>
        </div>
      </div>
        )
  }
}

